<template>
  <div class="index-bg">
    <Header title="">
      <template #leftTitle>
        <div class="address-wrap">
          <template v-if="address">
            <img class="img" src="@/assets/images/wallet.png" alt="">
            {{address.substring(0,8)}}...{{address.substring(address.length - 8, address.length)}}
          </template>
          <template v-else>
            <div class="address-button" @click="initWallet">
              {{t('index.')}}
            </div>
          </template>
        </div>
      </template>
    </Header>
    <van-swipe class="index-swipe" indicator-color="white">
      <van-swipe-item v-for="(item,index) in dataImage[local].banner" :key="index"><img :src="item.image" alt="" /></van-swipe-item>
    </van-swipe>
    <template v-if="local === 'zh-TW'">
      <img style="width:100%;height:auto;display:block;" src="@/assets/images/block1.webp" alt="">
    </template>
    <template v-else>
      <img style="width:100%;height:auto;display:block;" src="@/assets/images/block1-en.webp" alt="">
    </template>
