      <div class="left">
        <div class="title">IDO</div>
        <div class="progress">
          <div class="progress-text">
            {{Smeta.idoProgress}}%
          </div>
          <van-slider disabled v-model="Smeta.idoProgress" bar-height="20px" :min="0" :max="100" @change="onChangeSlider">
            <template #button>
              <div class="custom-button">
              </div>
            </template>
          </van-slider>
        </div>
        <div class="input-wrap">
          <input v-model="Smeta.amount" type="number" :placeholder="t('index.')">
        </div>
        <div class="des">
          <div class="p">IDO{{t('index.')}} 1U = {{Smeta.scale}} Smeta</div>
        </div>
        <div class="btn-wrap">
          <template v-if="Number(usdt.allowance)">
            <van-button type="primary" :loading="Smeta.loadingIDO" :disabled="Smeta.loadingIDO" :loading-text="t('index.')+'...'" @click="idoHandle">{{ t("index.") }}</van-button>
          </template>
          <template v-else>
            <van-button type="primary" :loading="usdt.loading" :disabled="usdt.loading" :loading-text="t('index.')+'...'" @click="approveUsdtHandle">{{ t("index.") }}</van-button>
          </template>
        </div>
      </div>
    </div> -->
    <template v-if="local === 'zh-TW'">
      <img style="width:100%;height:auto;display:block;" src="@/assets/images/block2.webp" alt="">
      <img style="width:100%;height:auto;display:block;" src="@/assets/images/block3.webp" alt="">
      <img style="width:100%;height:auto;display:block;" src="@/assets/images/block4.webp" alt="">
      <img style="width:100%;height:auto;display:block;" src="@/assets/images/block5.webp" alt="">
      <img style="width:100%;height:auto;display:block;" src="@/assets/images/block6.webp" alt="">
    </template>
    <template v-else>
      <img style="width:100%;height:auto;display:block;" src="@/assets/images/block2-en.webp" alt="">
      <img style="width:100%;height:auto;display:block;" src="@/assets/images/block3-en.webp" alt="">
      <img style="width:100%;height:auto;display:block;" src="@/assets/images/block4-en.webp" alt="">
      <img style="width:100%;height:auto;display:block;" src="@/assets/images/block5-en.webp" alt="">
      <img style="width:100%;height:auto;display:block;" src="@/assets/images/block6-en.webp" alt="">
    </template>
  </div>
</template>
<script>
import Header from '@/components/Header'
import { reactive, toRefs, computed, watch, getCurrentInstance } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import { getLang } from "@/utils/i18n";
import web3Data from "@/utils/initWeb3";
import { config } from "@/utils/config";
import { Toast } from "vant";
import { MaxUint256 } from "@ethersproject/constants";
import idoAbi from "@/utils/idoAbi.json";
import tokenAbi from "@/utils/abi";
import BigNumber from "bignumber.js";
export default {
  components: {
    Header
  },
  setup () {
    const { proxy } = getCurrentInstance();
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      balance: 0,
      local: getLang() || "zh-TW",
      dataImage: {
        'zh-TW': { banner: [{ image: require('@/assets/images/banner_en@2x.png') }] },
        'en': { banner: [{ image: require('@/assets/images/banner_en@2x.png') }] }
      },
      Smeta: {
        idoProgress: 0,
        scale: '--',
        amount: '',
        loadingReceive: false,
        loadingIDO: false
      },
      usdt: {
        allowance: 0,
        loading: false
      },
      idoContract: null,
      usdtContract: null,
      smetaContract: null,
      initContract: () => {
        data.idoContract = new data.web3.eth.Contract(
          idoAbi,
          config.IDO
        );
        data.usdtContract = new data.web3.eth.Contract(
          tokenAbi,
          config.usdtAddres
        );
        data.smetaContract = new data.web3.eth.Contract(
          tokenAbi,
          config.smetaAddress
        );
        data.initData()
      },
      initData: () => {
        data.getAllowanceUsdt()
        data.getScale()
        data.getProgress()
      },
      initWallet: () => {
        web3Data.initWeb3()
      },
      onChangeSlider: () => {
        console.log('idoProgress:', data.Smeta.idoProgress)
      },
      getAllowanceUsdt: async () => {
        let allowance = await data.usdtContract.methods
          .allowance(data.address, config.IDO)
          .call({ from: data.address });
        console.log(allowance, 'getAllowanceUsdt')
        data.usdt.allowance = allowance;
        return allowance;
      },
      approveUsdtHandle: () => {
        data.usdt.loading = true
        data.usdtContract.methods
          .approve(config.IDO, MaxUint256)
          .send({ from: data.address })
          .then(async () => {
            data.usdt.loading = false
            Toast(t("index."));
            await data.getAllowanceUsdt();
          })
          .catch((error) => {
            console.log(error)
            data.usdt.loading = false
            Toast(t("index."));
          });
      },
      getScale: async () => {
        let exchangeRate = await data.idoContract.methods.exchangeRate().call({
          address: data.address
        })
        let exchangeRateBase = await data.idoContract.methods.exchangeRateBase().call({
          address: data.address
        })
        let scale = new BigNumber(exchangeRate)
          .dividedBy(exchangeRateBase)
          .toFixed();
        data.Smeta.scale = scale
        console.log('scale:', data.Smeta.scale)
      },
      // idoHandle: async () => {
      //   try {
      //     if (!data.Smeta.amount) {
      //       return Toast(t("index."));
      //     }
      //     data.Smeta.loadingIDO = true
      //     let _usdtBalance = await data.usdtContract.methods.balanceOf(data.address).call({
      //       from: data.address
      //     })
      //     let _usdtDecimals = await data.usdtContract.methods
      //       .decimals()
      //       .call({ from: data.address })
      //     _usdtBalance = new BigNumber(_usdtBalance)
      //       .dividedBy(10 ** _usdtDecimals)
      //       .toFixed();
      //     if (new BigNumber(_usdtBalance).comparedTo(data.Smeta.amount) === -1) {
      //       data.Smeta.loadingIDO = false
      //       return Toast(t("index."));
      //     }
      //     let minAmount = await data.idoContract.methods.minAmount().call({
      //       from: data.address
      //     })
      //     let decimalsSmeta = await data.smetaContract.methods.decimals().call({
      //       from: data.address
      //     })
      //     minAmount = new BigNumber(minAmount)
      //       .dividedBy(10 ** decimalsSmeta)
      //       .toFixed();
      //     if (new BigNumber(minAmount).comparedTo(data.Smeta.amount) === 1) {
      //       data.Smeta.loadingIDO = false
      //       return Toast(t("index.key", { key: minAmount }));
      //     }
      //     console.log(minAmount, 'minAmount')
      //     console.log(_usdtBalance, '_usdtBalance', data.Smeta.amount)
      //     let usdtAmount = new BigNumber(data.Smeta.amount)
      //       .multipliedBy(10 ** _usdtDecimals)
      //       .toFixed();
      //     let _gas = await data.idoContract.methods
      //       .getToken(usdtAmount)
      //       .estimateGas({
      //         from: data.address
      //       });
      //     console.log(_gas, 'buy.estimateGas')
      //     let res = await data.idoContract.methods.getToken(usdtAmount).send({
      //       from: data.address
      //     });
      //     console.log(res, 'buy')
      //     data.Smeta.loadingIDO = false
      //     Toast(t("index.success"));
      //     data.Smeta.amount = ''
      //     data.getProgress()
      //   } catch (error) {
      //     data.Smeta.loadingIDO = false
      //     proxy.$errorformat(error)
      //   }
      // },
      getProgress: async () => {
        let circulation = 50000000
        // = 5000w*10**18-smeta /5000w*10**18*100%
        let balanceSmeta = await data.smetaContract.methods.balanceOf(config.IDO).call({
          from: data.address
        })
        console.log('balanceSmeta:', balanceSmeta)
        let decimalsSmeta = await data.smetaContract.methods.decimals().call({
          from: data.address
        })
        console.log('decimalsSmeta:', decimalsSmeta)
        balanceSmeta = new BigNumber(balanceSmeta)
          .dividedBy(10 ** decimalsSmeta)
          .toFixed();
        console.log('balanceSmeta:', balanceSmeta)
        data.Smeta.idoProgress = new BigNumber(circulation)
          .minus(balanceSmeta)
          .dividedBy(circulation)
          .multipliedBy(100)
          .toFixed(2);
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
img {
  user-select: none;
}
.index-bg {
  padding-top: 46px;
  background: #080033;
  color: #fff;
  .address-wrap {
    display: flex;
    align-items: center;
    .img {
      width: 18px;
      height: 18px;
      display: block;
      margin-right: 11px;
    }
    .address-button {
      background: #ed8c26;
      border-radius: 6px;
      color: #fff;
      padding: 2px 10px;
    }
  }
  .index-swipe {
    .van-swipe-item {
      width: 100%;
      height: 211px;
      img {
        width: 100%;
        height: 100%;
        display: block;
      }
    }
  }
  .ido-wrap {
    background: url(~@/assets/images/ido-bg.webp) no-repeat;
    background-size: 100% 100%;
    padding: 30px 15px;
    .left {
      width: 60%;
      .title {
        font-size: 24px;
        font-family: Microsoft YaHei;
        font-weight: bold;
        color: #ffffff;
      }
      .input-wrap {
        input {
          border-radius: 4px;
          width: 100%;
          height: 40px;
          padding: 0 10px;
          font-size: 14px;
          font-family: Microsoft YaHei;
          font-weight: 400;
          color: #111;
        }
      }
      .des {
        .p {
          margin-top: 7px;
          font-size: 12px;
          font-family: Microsoft YaHei;
          font-weight: 400;
          color: #ffffff;
        }
      }
      .btn-wrap {
        margin-top: 17px;
        justify-content: flex-start;
        .van-button {
          width: initial;
          min-width: 50%;
          height: 40px;
          line-height: 40px;
          background: #ed8c26;
          border-radius: 4px;
          font-size: 15px;
          font-family: Microsoft YaHei;
          font-weight: 400;
          color: #ffffff;
        }
      }
    }
  }
}
</style>
<style lang="less">
.progress {
  .van-slider {
    margin: 10px 0 20px;
    background: #080033;
    border-radius: 4px;
    &.van-slider--disabled {
      opacity: initial;
    }
    .van-slider__bar {
      background: #8f7fdd;
      background-image: repeating-linear-gradient(
        120deg,
        #b4a8eb,
        #b4a8eb 5px,
        transparent 0,
        transparent 10px
      );
    }
    .custom-button {
      width: 20px;
      height: 20px;
      position: absolute;
      top: 50%;
      transform: translate(-50%, -50%);
      left: 50%;
    }
  }
  .progress-text {
    font-size: 16px;
    font-family: PingFang SC;
    font-weight: bold;
    color: #cec5f8;
    line-height: 24px;
  }
}
</style>
