<template>
  <div class="head">
    <van-nav-bar :title="title" left-text="" :class="[{'active': headerActive},{'back-hader':Headtype === 'back'}]" left-arrow>
      <template #left>
        <template v-if="type === 'back'">
          <img class="back" src="@/assets/images/back@2x.png" @click="$goBack" alt="">
        </template>
        <template v-else>
          <span class="header-title">
            <slot name="leftTitle"></slot>
          </span>
        </template>
      </template>
      <template #right v-if="Headtype !== 'back'">
        <van-popover v-model:show="showPopover" placement="bottom-end" :actions="actions" @select="changeLanuage">
          <template #reference>
            <div class="lang-wrap flex-center">
              {{localFilter(local)}}
              <img class="arrDown" :class="{'arrUp': showPopover}" src="@/assets/images/arr-b.png">
            </div>
          </template>
        </van-popover>
      </template>
    </van-nav-bar>
  </div>
</template>

<script>
import { reactive, toRefs, computed } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import { getLang, setLang } from "@/utils/i18n";
import { intro } from "@/storage"
export default {
  props: {
    Headtype: {
      type: String,
      default: ""
    },
    title: {
      type: String,
      default: ""
    }
  },
  setup (props) {
    const store = useStore();
    const { t } = useI18n();
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      Headtype: props.Headtype,
      title: props.title,
      showPopover: false,
      headerActive: false,
      local: getLang() || "zh-TW",
      intro: intro.get() || '',
      actions: [
        {
          text: 'English',
          value: 'en'
        },
        {
          text: '',
          value: 'zh-TW'
        }
        // {
        //   text: '',
        //   value: 'ja'
        // },
        // {
        //   text: '',
        //   value: 'ko'
        // }
      ],
      changeLanuage: (val) => {
        setLang(val.value)
        data.local = val.value
        window.location.reload()
      },
      localFilter (value) {
        let list = data.actions
        let res = ''
        for (let k = 0; k < list.length; k++) {
          if (list[k].value === value) {
            res = list[k].text
            break
          }
        }
        return res
      }
    })
    const debounce = (fn, interval) => {
      var timer;
      var gapTime = interval || 150;
      return function () {
        clearTimeout(timer);
        var context = this;
        var args = arguments;
        timer = setTimeout(function () {
          fn.call(context, args);
        }, gapTime);
      };
    }
    document.addEventListener('scroll', debounce(() => {
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      data.headerActive = scrollTop > 30
    }))
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>
<style lang="less" scoped>
.head {
  color: #b1beea;
  font-size: 14px;
  font-family: PingFang SC;
  font-weight: 500;
  line-height: 35px;
}
.back {
  width: 10px !important;
  height: 18px;
}
.arrDown {
  width: 10px !important;
  height: 10px;
  margin-left: 9px;
}
.lang-wrap {
  min-width: 90px;
  height: 30px;
  border: 1px solid #cec5f8;
  border-radius: 4px;
}
.arrUp {
  transform: rotateX(180deg);
}
</style>
<style lang="less">
#app {
  .van-nav-bar {
    &.active {
      transition: all 0.3s;
      background: #35305a;
    }
    &.back-hader {
      transition: all 0.3s;
      background: #35305a;
      box-shadow: initial;
    }
  }
}
</style>
